html, body, #root {
  height: 100%;
  margin: 0;
  padding: 0;
  overflow: hidden;
}

.App {
  text-align: center;
}

.App-logo {
  height: 40vmin;
  pointer-events: none;
}

@media (prefers-reduced-motion: no-preference) {
  .App-logo {
    animation: App-logo-spin infinite 20s linear;
  }
}

.App-header {
  background-color: #282c34;
  min-height: 100vh;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  font-size: calc(10px + 2vmin);
  color: white;
}

.App-link {
  color: #61dafb;
}

@keyframes App-logo-spin {
  from {
    transform: rotate(0deg);
  }
  to {
    transform: rotate(360deg);
  }
}

.chat-architect {
  background: #18191a;
  color: #fff;
  padding: 24px;
  min-height: 100vh;
}

.messages {
  display: flex;
  flex-direction: column;
  gap: 8px;
}

.msg-bubble {
  max-width: 70%;
  padding: 10px 16px;
  border-radius: 18px;
  display: flex;
  align-items: flex-end;
  gap: 8px;
  margin: 4px 0;
  font-size: 1rem;
  position: relative;
  box-shadow: 0 1px 2px rgba(0,0,0,0.08);
}

/* A (left) bubble */
.msg-bubble.left {
  align-self: flex-start;
  background: #fff;
  color: #222;
  border-bottom-left-radius: 4px;
  border-bottom-right-radius: 18px;
  border-top-right-radius: 18px;
  border-top-left-radius: 18px;
  /* WhatsApp green border for A (optional) */
  border: 1px solid #ece5dd;
}

/* B (right) bubble */
.msg-bubble.right {
  align-self: flex-end;
  background: #dcf8c6; /* WhatsApp green */
  color: #222;
  border-bottom-right-radius: 4px;
  border-bottom-left-radius: 18px;
  border-top-right-radius: 18px;
  border-top-left-radius: 18px;
  border: 1px solid #b2e59e;
}

/* Style the textarea inside bubbles */
.msg-bubble textarea {
  width: 100%;
  min-height: 32px;
  border: none;
  border-radius: 8px;
  padding: 4px 8px;
  font-size: 1rem;
  resize: vertical;
  background: transparent;
  color: inherit;
  outline: none;
  margin-left: 0;
  margin-right: 0;
}

/* Remove textarea background for WhatsApp look */
.msg-bubble.left textarea,
.msg-bubble.right textarea {
  background: transparent;
  color: inherit;
  border: none;
  outline: none;
  box-shadow: none;
  padding: 0;
  margin: 0;
}

